<template>
  <header class="header">
    <transition name="header__notes">
      <div class="header__notes" v-show="header">

        <h1 class="header__title">Eslatmalar</h1>
        <button 
        class="header__search" 
        @click="header = !header, 
        $emit('openSearch', header)">
          <img src="@/assets/img/search.svg" alt="" />
        </button>
      </div>
    </transition>

    <transition name="header__form">
      <div class="header__form" v-show="!header">
      <button 
      class="header__back"
      @click="(header = !header), 
      (search = ''), 
      $emit('openSearch', header)">
        <img src="@/assets/img/arrow.svg" alt="" />
      </button>

      <input
        type="text"
        class="header__input"
        placeholder="Qidirish..."
        v-model="search"
      />

      <button class="header__close" @click="search = ''">
        <img src="@/assets/img/close.svg" alt="" />
      </button>
    </div>
    </transition>
  </header>
</template>

<script>
export default {
  name: "Header-vue",
  data() {
    return {
      header: true,
      search: "",
    };
  },
  watch: {
    search: {
      handler(val) {
        this.$emit('getSearch', val)
      }
    }
  },


};
</script>

<style>
.header {
  background: #f3edf7;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 1px 3px rgba(0, 0, 0, 0.3);
  height: 64px;
  padding: 18px 20px;
  overflow: hidden;
}

.header__notes,
.header__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__lang {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  color: #1c1b1f;
}

.header__title {
  font-size: 22px;
  line-height: 28px;
  font-weight: 400;
  color: #1c1b1f;
}

.header__input {
  border: none;
  outline: none;
  background: transparent;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #1c1b1f;
  display: block;
  width: 90%;
}

.header__input::placeholder {
  color: #9d9d9d;
}


.header__notes-enter-active,
.header__notes-leave-active{
  transition: 0.5s linear;
}

.header__notes-enter-from,
.header__notes-leave-to{
  opacity: 0;
  transform: translateY(-200px);
}

.header__form-enter-active,
.header__form-leave-active{
  transition: 0.5s linear;
}

.header__form-enter-from,
.header__form-leave-to{
  opacity: 0;
  transform: translateY(200px);
}

.header__form-enter-to,
.header__form-leave-from{
  opacity: 1;
  transform: translateY(0px);
}


</style>