<template>
  <Header @getSearch="search = $event"/>
  <Notes
    :notes="filterNotes"
    @delNote="delNote"
    @changeMessage="changeNote"
    :search="search"
  />
  <Modal
    :currentId="currentId"
    v-show="modalOpen"
    @closeModal="closeModal"
    @addNote="addNote"
    :edit="edit"
    :editMessage="editMessage"
    @editedNote="editedNote"
  />
  <AddModal @openModal="openModal" v-show="!modalOpen" />
</template>

<script>
import Header from "@/components/Header.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
import AddModal from "@/components/AddModal.vue";

export default {
  components: {
    Header,
    Notes,
    Modal,
    AddModal,
  },
  data() {
    return {
      currentId: 1,
      search: "",
      modalOpen: false,
      notes: [],
      edit: false,
      editMessage: {},
      lang: "uz",
      langWords: {},
    };
  },

  created() {
    this.getNote();
    // this.langWords = Langs
    // localStorage.words = JSON.stringify(this.langWords)
    // localStorage.lang = localStorage.lang || 'uz'
    // this.lang = localStorage.lang || 'uz'
  },


  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((item) =>
            item.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },

  methods: {
    openModal() {
      this.modalOpen = true;
    },
    closeModal(status) {
      this.modalOpen = status;
      setTimeout(() => (this.edit = false), 500);
    },

    addNote(message) {
      this.notes.push(message);
      this.modalOpen = false;
    },

    delNote(id) {
      const index = this.notes.findIndex((el) => el.id == id);
      this.notes.splice(index, 1);
    },

    getNote() {
      const localNotes = localStorage.notes;

      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },

    changeNote(id) {
      this.edit = this.modalOpen = true;
      this.result = this.notes.find((note) => note.id === id);
      this.editMessage = this.result;
    },

    editedNote(answer) {
      this.notes.forEach((note) => {
        if (note.id == answer.id) {
          (note.title = answer.title),
            (note.descr = answer.descr),
            (note.date = answer.date);
        }
      });
    }
  },

  watch: {
    notes: {
      handler(newNotes) {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>

