<template>
  <div class="notes">
    <div class="container">
      <div class="notes__top">
        <h2 class="notes__title">
          <span v-if="search"> 
            Qidirish
          </span>
          <span v-else>
            {{ notes.length > 0 ? 'Barcha eslatmalar' : 'Eslatmalar yo`q'}}
          </span>
        </h2>

        <button class="notes__btn" @click="grid = !grid">
          <img src="@/assets/img/categories.svg" alt="" v-if="grid" />
          <img src="@/assets/img/categories2.svg" alt="" v-else />
          <span>{{ grid ? "Ro'yxat" : "Setka" }}</span>
        </button>
      </div>

      <div class="notes__list" :class='{active: !grid}'>
        <NotesItem 
        :grid='grid' 
        v-for="note in notes"
        :key='note.id'
        :note='note'
        @delNote="$emit('delNote', note.id)"
        @changeMessage="$emit('changeMessage', note.id)"
        :search="search"
        />
      </div>
    </div>
  </div>
</template>



<script>
import NotesItem from "@/components/NotesItem.vue";

export default {
  components: {
    NotesItem,
  },
  data() {
    return {
      grid: true,
    };
  },
  props: {
    notes: {
      typeof: Array,
      required: true
    },
    search: String
  }
};
</script>
<style>
.notes {
  margin-top: 30px;
}

.notes__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.notes__title {
  font-weight: 400;
  font-size: 22px;
  line-height: 28px;
}

.notes__btn {
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11),
      rgba(103, 80, 164, 0.11)
    ),
    #fffbfe;

  box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15),
    0px 1px 3px rgba(0, 0, 0, 0.3);
  border-radius: 16px;
  min-width: 120px;
  min-height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 15px;
}

.notes__btn span {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #6750a4;
}

.notes__btn img {
  width: 20px;
  height: 20px;
}

.notes__list {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}

.notes__list.active{
  grid-template-columns: 1fr;
}
</style>