<template>
  <Transition name="modal__block">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__title">
          {{edit ? 'Eslatmani tahrirlash' : 'Eslatma qo’shish'}}
        </h2>

        <div class="modal__inputs">
          <label>
            <span>Title</span>
            <input
              class="modal__inputs-comment"
              type="text"
              placeholder="Title"
              name="name"
              v-model="title"
              autocomplete="off"
            />
          </label>

          <label>
            <span>Content</span>
            <textarea
              class="modal__inputs-comment"
              placeholder="Content"
              name="comment"
              v-model="descr"
            ></textarea>
          </label>
        </div>

        <div class="modal__btns">
          <button class="modal__buttons close" @click="closeModal">
            Bekor qilish
          </button>
          <button v-if="edit" class="modal__buttons add" @click="changeMessage">O'zgartirish</button>
          <button v-else class="modal__buttons add" @click="addNote">Qo’shish</button>
        </div>
      </div>
    </div>
  </Transition>
</template>
<script>
export default {
  props: {
    currentId: Number,
    edit: Boolean,
    editMessage: Object
  },
  data() {
    return {
      id: this.currentId,
      title: "",
      descr: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
      this.title = "";
      this.descr = "";
    },

    addNote() {
      if (this.title != "" && this.descr != "") {
        const message = {
          id: this.id++,
          title: this.title,
          descr: this.descr,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", message);
        this.title = "";
        this.descr = "";
      }
    },

    changeMessage() {
      if(this.title != '' && this.descr != '') {
        const editedNote = {
          id: this.editMessage.id,
          title: this.title,
          descr: this.descr,
          date: new Date().toLocaleDateString()
        }
        this.$emit('editedNote', editedNote);
        this.closeModal();
      }
    }


  },
};
</script>

<style>
.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.35);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__block {
  padding: 24px;
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11),
      rgba(103, 80, 164, 0.11)
    ),
    #fffbfe;
  border-radius: 28px;
}

.modal__title {
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
}

.modal__inputs {
  display: flex;
  flex-direction: column;
}

.modal__inputs label {
  display: flex;
  position: relative;
  margin-top: 16px;
}

.modal__inputs span {
  position: absolute;
  top: 8px;
  left: 15px;
  font-size: 12px;
  color: #6750a4;
  letter-spacing: 0.4px;
  font-weight: 400;
}

.modal__inputs-comment {
  width: 264px;
  height: 56px;
  display: block;
  border: none;
  outline: none;
  background: #e7e0ec;
  border-radius: 4px 4px 0px 0px;
  border-bottom: 1px solid #1c1b1f;
  padding: 25px 15px 8px;
  resize: none;
}

.modal__btns {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 30px;
  gap: 8px;
}

.modal__buttons {
  padding: 10px 12px;
  transition: 0.2s;
  background: transparent;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.1px;
  opacity: 0.6;
}

.close {
  color: #cf1b1b;
}

.add {
  color: #6750a4;
}

.modal__buttons:hover {
  opacity: 1;
}

.modal__block-enter-active,
.modal__block-leave-active {
  transition: opacity 0.5s ease;
}

.modal__block-enter-from,
.modal__block-leave-to {
  opacity: 0;
}
</style>